body {
    @include reg();
    color: $main;
}

.fadein {
    visibility: hidden;
}

// Fonts

h1, .h1 {
    @include reg();
    .sub-title {
        display: block;
        font-size: 70%;
        @include med();
        opacity: 0.5;
    }
}

h2, .h2 {
    @include med();
    line-height: 1.4;
    font-size: 40px;
}

h3, .h3 {
    font-size: 28px;
    line-height: 1.4;
}

h4 {
    line-height: 1.6;
    letter-spacing: 0.025em;
}

h5, .h5 {
    font-size: 22px;
}

p {
    font-size: 20px;
    line-height: 28px;
}

a, button {
    letter-spacing: 0.05em;
    color: $main;
    text-decoration: none !important;
    &:hover, &:focus {
        outline: none;
        color: $main;
    }
}

.bold {
    @include bold();
}

.italic {
    @include italic();
}

.white {
    color: #fff;
}

.slide-item {
    .entry {
        p {
            margin: 0 auto;
        }
    }
}

// Globals

main, .main {
    padding-left: 0 !important;
    padding-right: 0 !important;
}

.green-bg {
    background-color: $green;
}

.red-bg {
    background-color: $red;
}

.blue-bg {
    background-color: $blue;
}

.doc {
    padding: 80px 0;
    position: relative;
}

.leaves-bg {
    position: relative;
    &:before {
        content:'';
        position: absolute;
        top: 0;
        left: 0;
        background:  url('../images/leaf-1.png') no-repeat center;
        background-size: 100%;
        width: 10%;
        height: 100%;
        background-position: 50% 20%;
    }
    &:after {
        content:'';
        position: absolute;
        bottom: 0;
        right: 0;
        background: url('../images/leaf-2.png') no-repeat center;
        background-size: 100%;
        width: 10%;
        height: 100%;
        background-position: 50% 100%;
    }
}

.wavy-bg {
    position: relative;
    &:before {
        content:'';
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        background: url('../images/wavy.png') no-repeat center;
        background-size: contain;
        width: 100%;
        height: 100%;
        background-position: 50% 0;
    } 
}

.entry { 
    padding: 20px;
    &.video-wrap {
        background: #fff;
    }
}
 
.sec-heading {
    padding: 40px;
    h1 {
        color: #fff;
        margin: 0 auto;
    }
}

.flex-items-xs-middle {
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
}

.no-gutters {
  margin-right: 0;
  margin-left: 0;

  > .col,
  > [class*="col-"] {
    padding-right: 0;
    padding-left: 0;
  }
}

.leaf {
    margin: 0 auto;
    max-width: 100px;
    &.smallest {
        max-width: 50px;
        margin: 0 auto 40px;
    }
}

.black-leaf {
    background: url('../images/black-leaf.png') no-repeat center;
    background-size: 80%;
    width: 50px;
    height: 50px;
    display: inline-block;
    margin-right: -50px;
    margin-bottom: -10px;
    &.lefty {
        margin-right: auto;
        margin-left: -50px;
    }
}

// Intro

.intro {
    position: relative;
    width: 100%;
    .hero {
        height: 90vh; 
    }
    .scroll-down {
        position: absolute;
        bottom: -20px;
        left: 50%;
        margin-left: -20px;
        width: 40px;
        height: 40px;
        background: #fff;
        text-align: center;
        line-height: 45px;
        border-radius: 50%;
        font-size: 28px;
        @include shadow();
        @include cubic();
        opacity: 0;
        transform: translateY(100px);
        z-index: 1;
        &.active {
            opacity: 1;
            transform: translateY(0);
        }
        &:hover, &:focus {
            background: #000;
            color: #fff;
        }
    }
}

.slick-slide {
    position: relative;
}

.hero-entry {
    position: absolute;
    left: 50%;
    top: 48%;
    transform: translateY(-50%) translateX(-50%);
    text-align: center;
    color: #fff;
    max-width: 1000px;
    margin: 0 auto;
    padding: 0 20px;
    z-index: 1;
    width: 100%;
    h1 {
        font-size: 3rem;
        @include light();
        margin: 0 auto;
        line-height: 1.4;
        span {
            @include bold();
        }
    }
    a {
        margin: 60px auto 0;
    }
}

.intro-slider {
    .scroll-down {
        position: absolute;
        bottom: -20px;
        left: 50%;
        margin-left: -20px;
        width: 40px;
        height: 40px;
        background: #fff;
        text-align: center;
        line-height: 45px;
        border-radius: 50%;
        font-size: 28px;
        @include shadow();
        @include cubic();
        opacity: 0;
        transform: translateY(100px);
        z-index: 1;
        &.active {
            opacity: 1;
            transform: translateY(0);
        }
        &:hover, &:focus {
            background: #000;
            color: #fff;
        }
    }
}
.full-height {
    height: 90vh;

}

.cta-entry {
    padding: 80px 0;
    text-align: center;
    color: #fff;
    max-width: 960px;
    margin: 0 auto;
    &.dark {
        color: #000;
    }
    h2 {
        margin: 20px auto;
        @incldue med();
    }
    a {
        margin: 40px auto 0;
    }
}

.box-entry {
    height: 600px;
    color: #fff;
    text-align: center;
    position: relative;
    .inner-entry {
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        left: 0; 
        right: 0;
        max-width: 500px;
        margin: 0 auto;
        &.has-curve {
            padding:60px 20px 0;
        }
        h3 {
            margin: 0 auto 40px;
            font-size: 1.75rem; 
        }
    }
}

// Curved Sections

.curved-top {
    position: relative;
    &:before {
        content:'';
        position: absolute;
        top:0;
        left: 0;
        right: 0;
        height: 200px;
        background: url('../images/cuved-top.png') no-repeat center;
        background-size: cover;
        z-index: 1;
    }
}

.curved-bottom {
    position: relative;
    &:before {
        content:'';
        position: absolute;
        bottom: -200px;
        left: 0;
        right: 0;
        height: 200px;
        background: url('../images/curved-bottom.png') no-repeat center;
        background-size: cover;
        z-index: 1;
    }
}

.pad-top {
    padding-top: 200px;
    &.lg {
        padding-top: 400px;
    }
}

.laying-ipad {
    margin-right: -100px;
    margin-left: -100px;
}

// Testimonials

.testimonials {
    color: #fff;
    text-align: center;
    .testimonial-img {
        max-width: 150px;
        margin: 0 auto 50px;
    }

    .testimonial {
        @include italic();  
        font-size: 24px;
    }
}

.dictionary-result {
    border: 1px solid #000;
    padding: 300px 20%;
    text-align: center;
    margin: 80px auto 0;
}

.dictionary-input {
    input {
        border-radius: 0;
        margin: 40px auto 0;
        padding: 15px;
    }
}

// Team

.team-img {
    position: relative;
    display: block;
    text-align: center;
    img {
        border-radius: 50%;
        margin: 0 auto;
    }
    .team-desc {
        .team-name {
            background: #fff;
            padding: 15px 0 0;
            position: relative;
            @incldue bold(); 
        }
    }
}

// Sliders

.slider-wrap {
    position: relative;
}

.slider-arrow {
    position: absolute;
    top: 50%;
    display: block;
    width: 50px;
    height: 50px;
    text-align: center;
    font-size: 32px;
    line-height: 50px;
    margin-top: -25px;
    background: $grey;
    color: #fff;
    border-radius: 50%;
    &.next {
        right: -60px;
    }
    &.prev {
        left: -60px;
    }
    &.full-width {
        background: none;
        border: 1px solid #fff;
        &.next {
            right: 60px;
        }
        &.prev {
            left: 60px;
            z-index: 1;
        }
    }
}

// App Page

.icons {
    .icon {
        border: 1px solid #fff;
        padding: 30px 0;
        position: relative;
        .icon-img {
            max-width: 80px;
            margin: 0 auto 20px;
            position: relative;
        }
        p {
            margin: 0 auto;
            font-size: 16px;
            text-align: center;
            color: #fff;
            @include bold();
            letter-spacing: 0.05em;
        }
        .overlay-copy {
            top: 0;
            left: 0;
            right: 0;
            bottom: 0;
            position: absolute;
            background: #fff;
            z-index: 1;
            padding: 20px 5px;
            @include easing();
            opacity: 0;
            p {
                color: #000;
                @include reg();
                margin: 0 auto;
                line-height: 1.2;
            }
        }
        &:hover {
            .overlay-copy {
                opacity: 1;
            }
        }
    }
}

.app-module {
    margin: 120px auto;
    .ipad-frame {
        position: absolute;
        z-index: 1;
        top: 0;
        left: 0;
        right: 0;
    }
    .frame-img {
        position: absolute;
        z-index: 1;
        top: 0;
        left: 0;
        right: 0;
    }
    .app-img { 
        padding: 0 20px;
        position: relative;
        height: 100%; 
        min-height: 800px;
        .image-holder {
            //opacity: 0;
            position: absolute;
            left: 0;
            right: 0;
            height: 100%;
            //@include easing();
            //transform: scale(0.5);
            &.active {
                //@include easing();
                //opacity: 1; 
                //transform: scale(1);
            }
            
            .ipad-screen, .desktop-screen {
                position: absolute;
                top: 0;
                width: 100%;
                height: auto;
                z-index: -1;
                padding: 10px;
            }
            .desktop-screen {
                padding: 30px;
            }
            
        }
        &.desktop-mod {
            max-width: 700px;
            margin: 0 auto;
            min-height: 550px;
        }
    }
    .app-link {
        position: relative;
        display: block;
        cursor: pointer;
        @include cubic();
        z-index: 1;
        margin: 20px auto 0;
        opacity: 1;
        &.ipad-mod {
            margin: 20px auto 60px;
        }
        &:hover, &:focus {
            color: #000;
        }
        &:before {
            content:'';
            position: absolute;
            left: -60px;
            background: url('../images/black-leaf.png') no-repeat center;
            background-size: 80%;
            width: 40px;
            height: 40px;
            display: inline-block;
            opacity: 1;
        }
        
        &.not-this {
            &:before {
                opacity: 0;
            }
        }
        &.active {
            &:before {
                opacity: 1;
            }
        }
        
        &:hover, &:focus {
            color: #000;
            
        }
        h3 {
            margin: 0 auto 10px;
            @include bold();
        }
        p {
            font-size: 20px;
        }
        &:hover {
            h3 {
                color: $green;
            }
            &:before {
                background: url('../images/green-leaf.png') no-repeat center;
                background-size: 80%;
            }
        }
        &.righty {
            text-align: right;
            &:before {
                left: auto;
                right: -60px;
            }
        }
        .animated-line {
            position: absolute;
            left: -40px;
            height: 600px;
            background: $blue;
            width: 2px;
            bottom: 90%;
            opacity: 0;
            @include easing();
            &:before {
                content: "";
                position: absolute;
                background: $blue;
                width: 300px;
                height: 2px;
                left: 0;
                top: 0;
            }
            &:after { 
                content: "";
                position: absolute;
                background: $blue;
                width: 30px;
                height: 30px;
                left: 300px;
                top: -15px;
                border-radius: 50%;
                margin-left: -15px;
            }
            &.right-line {
                left: auto;
                right: -40px;
                bottom: 70px;
                &:before {
                    left: -300px;
                }
                &:after {
                    right: auto;
                    left: -315px;
                }
                &.line-6 {
                    bottom: 90px;
                    height: 500px;
                    &:before {
                        width: 280px;
                        left: -280px;
                    }
                    &:after {
                        left: -290px;
                    }
                }
                &.line-8 {
                    height: 450px;
                    bottom: 80px;
                    &:before {
                        left: -500px;
                        width: 500px;
                    }
                    &:after {
                        left: -500px;
                    }
                }
            }
            &.line-5 {
                height: 430px;
            }
            &.line-7 {
                height: 535px;
            }
        }
        
        &.active {
            .animated-line {
                opacity: 1;
            }
        }
        &.desktop-mod {
            &.active {
                &:before {
                    background: url('../images/blue-leaf.png') no-repeat center;
                    background-size: 80%;
                }
                h3 {
                    color: $blue;
                }
            }
        }
    }
}

@media (max-width: 1200px) {
    .app-module {
        .app-link {
            .animated-line {
                &.line-5 {
                    &:before {
                        width: 220px;
                    }  
                    &:after {
                        left: 220px;
                    }
                } 
                &.line-6.right-line {
                    height: 500px;
                    bottom: 80px;
                    &:before {
                        width: 180px; 
                        left: -180px;
                    }
                    &:after {
                        left: -185px;
                    }
                }   
                &.line-7 {
                    height: 565px;
                }
                &.line-8.right-line {
                    height: 470px;
                    &:before {
                        left: -400px;
                        width: 400px;
                    }
                    &:after {
                        left: -400px;
                    }
                } 
            }
        }
    }
}


@media (max-width: 992px) {
    .app-module .app-link .animated-line {
        display: none;
    }
}

// Modals

.modal {
    .modal-dialog {
        max-width: 800px;
        width: 100%;
        .modal-content {
            border-radius: 0;
            padding: 60px;
            background: $blue;
            color: #fff;
            border: none;
            text-align: center;
        }
    }
    .close {
        position: absolute;
        right: 20px;
        top: 20px;
        background: $red;
        width: 50px;
        height: 50px;
        opacity: 1;
        text-shadow: none;
        border-radius: 50%;
        color: #fff;
        &:hover, &:focus {
            background: #fff;
            color: #000;
        }
    }
    &.fullscreen {
        .modal-dialog {
            margin: 0 auto;
            max-width: 100%;
            width: 100%;
            .modal-content {
                height: 100vh;
                .modal-content {
                    text-align: center;
                }
            }
        }
    }
}

// Contact

div.wpcf7 {
    padding: 0 60px;
}
.wpcf7 {
    input, textarea {
        border: none;
        padding: 10px 15px;
        background: rgba(255,255,255,0.2);
        @include cubic();
        border: 1px solid rgba(255,255,255, 0);
        color: #fff;
        font-size: 20px;
        @include reg();
        width: 100%;
        &:focus {
            outline: none !important;
            border: 1px solid rgba(255,255,255,1);
        }
    }
    input[type="submit"] {
        background: $green;
        color: #fff;
        @include bold();
    }
}

div.wpcf7-mail-sent-ok {
    border: none;
    background: #fff;
    padding: 10px;
    color: $green;
    font-size: 22px;
    margin: 0 auto;
}

::-webkit-input-placeholder { /* Chrome/Opera/Safari */
  color: #fff;
}
::-moz-placeholder { /* Firefox 19+ */
  color: #fff;
}
:-ms-input-placeholder { /* IE 10+ */
  color: #fff;
}
:-moz-placeholder { /* Firefox 18- */
  color: #fff;
}

@media (max-width: 575px) {
    
    body {
        width: 100% !important;
        overflow-x: hidden !important;
    }    
    
    .h1, h1 {
        font-size: 2rem;
    }

    h2, .h2 {
         font-size: 1.5rem;
    }
    
    .h3, h3 {
        font-size: 1.4rem;
    }
    
    .h4, h4 {
        font-size: 1.2rem;
    }

    p {
        font-size: 1.1rem;
    }    
    
    .intro {
        .hero {
            height: 75vh;
        }
        .hero-entry {
            h1 {
                font-size: 2rem;
            }
        }
    }

    .doc {
        padding: 20px 0;
        &.curved-top {
            padding: 60px 0 20px;
        }
    }
    
    .entry {
        padding: 20px 40px;
    }

    .box-entry {
        height: 500px;
    }
    
    .pad-top {
        padding-top: 50px;
        &.lg {
            padding: 0;
        }
    }

    .team-img {
        margin: 0 auto 40px;
        img {
            width: 100%;
        }
        .team-desc {
            bottom: -40px;
            top: auto;
            left: 0;
            .team-name {
                padding: 15px 30px 0;
            }
            .team-meta {
                font-size: 16px;
            }
            &.left {
                left: 0;
                .team-meta {
                    margin-left: 0;
                }
            }
        }
    }
    
    .laying-ipad {
        margin: 0 auto;
        padding: 0;
    }
    
    .footer-cta {
        padding: 60px 0 0;
    }
    
    div.wpcf7 {
        padding: 0;
    }
    
    .modal-dialog {
        margin: 0 auto !important;
    }
    .modal .modal-dialog .modal-content {
        padding: 60px 20px;
    }
    
    .slider-arrow {
        z-index: 1;
        &.prev {
            left: 0; 
        }
        &.next {
            right: 0;
        }
    }
    
    .app-module .app-link {
        padding: 0 40px; 
        &:before {
            left: 0;
        }
    }
     
    .app-module .app-img {
        min-height: 400px;
        max-width: 250px;
        margin: 0 auto;
    }    
    
    .app-module .app-img.desktop-mod {
        min-height: 300px;
    }
    
    .app-module .app-link.righty:before {
        right: 0;
    }
    
    .icons .icon {
        margin: 0 auto 20px;
    }
    
    .slider-arrow.full-width.prev {
        left: 20px;
    }
    
    .slider-arrow.full-width.next {
        right: 20px;
    }
    
    .hero-entry {
        h1 {
            font-size: 2rem;
        }
    }
    
}

.embed-responsive-4by3:before {
    padding-top: 150% !important;
}

.pdf_button {
    margin: 40px auto 0;
}











